@import "../../stylesheets/mixins", "../../stylesheets/reset",
  "../../stylesheets/variables";

.page__footer {
  width: 100%;
  height: 110px;
  border-top: solid 1px #f5f5f5;
  background-color: rgb(20, 25, 43);
  transition: all 1s ease;

  @include tablet {
    height: 60px;
  }

  &__box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 6px 0px;

    @include tablet {
      flex-direction: row;
      justify-content: space-between;
      padding: 18px 40px;
    }

    &__paragraph {
      font-family: $fontFamily;
      font-size: 14px;
      opacity: 0.5;
      color: white;
    }
  }

  & a {
    width: 103px;
    height: 40px;
    padding: 12px 0px;

    @include tablet {
      padding: 0px;
    }

    & img {
      width: 100%;
    }
  }
}


.joker {
  .page__footer {
  background-color: rgb(37, 36, 36);

  &__box {
    &__paragraph {
      font-family: $fontFamily;
      font-size: 14px;
      opacity: 0.5;
      color: white;
    }
  }
  }
}

.heart {
  .page__footer {
    background-color: white;

  &__box {
    &__paragraph {
      color: $colorMedium;
    }
  }
  }
}
